import React, { Component } from 'react';

class BoardCard extends Component {
  constructor(props) {
    super(props);

    let  { id, type, checked, classes } = this.props.board;
    this.state = {
      id, type, checked, classes
    }
  }

  componentDidMount() {
    this.cardClicked(true);
  }

  checkBoard(turnSymbol) {
    if(turnSymbol === 'x') {
      return String.fromCodePoint(10799);
    } else {
      return String.fromCodePoint(9898);
    }
  }

  cardHoveredEnter(e) {
    if(!this.state.checked) {
      this.setState({classes: "board-card center-items show"})
    }
  }

  cardHoveredOut() {
    if(!this.state.checked) {
      this.setState({classes: "board-card center-items"})
    }
  }

  cardClicked(fs) {
    let check;
    if(fs===true) {
      check = this.state.checked;
    } else {
      check = !this.state.checked;
    }
    if(check) {
      if(this.props.turn === 'x') {
        this.setState({classes: "board-card center-items x-mark show"});
      } else {
        this.setState({classes: "board-card center-items o-mark show"});
      }
      
      this.setState({ checked: true });
      this.setState({ type: this.props.turn });
  
      this.props.checkedTurn(this.state.id, this.props.turn, !this.state.checked, this.state.classes);
    }
  }

  render() {
    console.log('board card restart', this.props.restart);
    return(
      <div 
      onMouseOver = {this.cardHoveredEnter.bind(this)} 
      onMouseOut = {this.cardHoveredOut.bind(this)} 
      onClick={this.cardClicked.bind(this, false)} 
      className={this.state.classes}>
        {this.state.checked?this.checkBoard(this.state.type):this.checkBoard(this.props.turn)}
      </div>
    )
  }
}

export default BoardCard;