import React, { Component } from 'react';


// Custom Components
import BoardCard from './BoardCard';

class GameBoard extends Component {
  constructor(props) {
    super(props);

    this.history = [];
    this.brdcls = 'board-card center-items';
    this.initBoard = [
      {id: 1, type: null, checked: false, classes: this.brdcls},
      {id: 2, type: null, checked: false, classes: this.brdcls},
      {id: 3, type: null, checked: false, classes: this.brdcls},
      {id: 4, type: null, checked: false, classes: this.brdcls},
      {id: 5, type: null, checked: false, classes: this.brdcls},
      {id: 6, type: null, checked: false, classes: this.brdcls},
      {id: 7, type: null, checked: false, classes: this.brdcls},
      {id: 8, type: null, checked: false, classes: this.brdcls},
      {id: 9, type: null, checked: false, classes: this.brdcls},
    ];

    this.state = {
      boards: this.initBoard,
      turn: this.props.setTurn,
      winner: null,
      restart: false
    }
  }

  componentDidUpdate(prevProp) {
    if(prevProp.setTurn!==this.props.setTurn) {
      this.setState({ turn: this.props.setTurn });
    }
  }

  checkedTurn(id, type, checked, classes) {
    let turn;
    if(this.state.turn==='x') {
      turn = 'o';
    } else {
      turn = 'x';
    }
    this.setState({turn});

    let b = {
      id, type, checked, classes
    }

    let boards = this.state.boards.map(( board ) => {
      if(board.id === b.id) {
        board = b;
      }
      return board;
    });

    this.history.push(boards);
    this.setState({
      boards
    });
    console.log('HISTORY: ', this.history);
    this.chackGameWinner(boards);
  }


  chackGameWinner(boards) {
    let winner=null;
    let possibleLines = [
      [0,1,2],
      [0,3,6],  
      [0,4,8],   
      [2,5,8],   
      [2,4,6],
      [1,4,7],  
      [3,4,5],   
      [6,7,8]
    ];
    for(let i = 0; i < possibleLines.length; i++) {
      const [ a, b, c ] = possibleLines[i];
      if((boards[a].type===boards[b].type && 
        boards[b].type===boards[c].type)
        &&(boards[a].checked===true &&
        boards[b].checked===true &&
        boards[c].checked===true)) {
        winner = boards[a].type;
        break;
      }
    }
    if(winner==='x' || winner==='o') {
      this.setState({winner})
    }
  }

  restart() {
    console.log('restarting game....')
  }

  render() {
    console.log(this.state)
    return(
      <div className="board-card-container">
        {
          this.state.boards.map((boarditem) => {
            return (
              <BoardCard 
              board={boarditem} 
              turn={this.state.turn} 
              checkedTurn={this.checkedTurn.bind(this)}
              key={boarditem.id} />
            )
          })
        }
        { this.state.winner?
          <div className="winner-board center-items">
            <div className="winner-board-ontainer">
              Player: {this.state.winner} Win's!
            </div>
            <button className="restart-btn" onClick={this.restart.bind(this)}>Restart</button>
          </div> : null
        }
      </div>
    )
  }
}

export default GameBoard;