import React, { Component } from 'react';

// Custom Components
import Header from './components/Header';
import GameMenu from './components/GameMenu';
import Game from './components/Game';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showGame: false,
			turn: null
		}
	}

	handleShowGame(showGame) {
		this.setState({ showGame });
	}

	setTurn(t) {
		this.setState({turn: t})
	}

	restart(r, t) {
		this.setState({showGame: r});
		this.setState({turn: t});
	}

	render() {
		return(
			<div className="container">
				<Header />
				<div className="menu-card-container">

					<div className={this.state.showGame?"menu-card menu-card-one center-items hide--menu" : "menu-card menu-card-one center-items"}
					>
						<GameMenu handleShowGame={this.handleShowGame.bind(this)} setTurn={this.setTurn.bind(this)}/>
					</div>
					
					<div className={this.state.showGame?"menu-card menu-card-two  show-game" : "menu-card menu-card-two"}>
						<Game setTurn={this.state.turn} restart={this.restart.bind(this)}/>
					</div>

				</div>
			</div>
		)
	}
}

export default App;