import React, { Component } from 'react';

// Custom Components
import Timer from './game_components/Timer';
import GameBoard from './game_components/GameBoard';

class Game extends Component {
  restart(r) {
    this.props.restart(r);
  }

  render() {
    return(
			<div>
				<div className="game-card-content">
          <div className="game-timer-container">
            <Timer />
          </div>
					<div className="game-board-container">
            <GameBoard setTurn={this.props.setTurn} restart={this.restart.bind(this)}/>
          </div>
				</div>
			</div>
    )
  }
}

export default Game;
