import React, { Component } from 'react';

import play from '../play.png';
import pause from '../pause.png';

// Custom Component
import MenuCard from './MenuCard'; 

class GameMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMenu: false,
      showGame: false,
      menuItemToggler: false,
      menuType: null,
      gameStarted: false,
      turn: null
    }
  }
 
  menuToggler(e) {
    this.setState((state, props) =>  ({ showMenu: !state.showMenu }));
  }

  setTurn(turn) {
    this.props.setTurn(turn);
    this.setState({gameStarted: true});
    this.setState({menuItemToggler: false});
    this.setState({ showMenu: false });
    this.setState({showGame: !this.state.showGame})
    this.props.handleShowGame(!this.state.showGame);
  }

  openMenu(menuType) {
    this.setState({menuItemToggler: true});
    this.setState({menuType});
    this.setState({menuItemToggler: true});
  }

  closeMenu() {
    this.setState({menuItemToggler: false});
  }

  play() {
    if(!this.state.gameStarted) {
      this.setState({menuItemToggler: true});
      this.setState({menuType: 'play'});
    } else {
      this.props.handleShowGame(!this.state.showGame);
      this.setState((state, props) => ({
          showGame: !state.showGame
      }));
    }
  }

  render() {
    let menusDisplay;
    if(this.state.menuType === 'play') {
     menusDisplay =  <MenuCard toggleCard={this.state.menuItemToggler}
          closeMenu={this.closeMenu.bind(this)}>
        <div className="menuContainer playMenu">
          <p>Choose A Character</p>
          <div className="playMenuChoice">
            <span  role="img" aria-label="player-x" onClick={this.setTurn.bind(this,'x')}>&#10799;</span>
            <span  role="img" aria-label="player-o" onClick={this.setTurn.bind(this,'o')}>&#9898;</span>
          </div>
        </div>
      </MenuCard>
    }else if(this.state.menuType === 'help') {
      menusDisplay =  <MenuCard toggleCard={this.state.menuItemToggler}
           closeMenu={this.closeMenu.bind(this)}>
         HELP
       </MenuCard>
     }else if(this.state.menuType === 'about') {
      menusDisplay =  <MenuCard toggleCard={this.state.menuItemToggler}
           closeMenu={this.closeMenu.bind(this)}>
          <div className="menuContainer">
            <span className="about_title">
              Developed By: Leul (dash-dev)
            </span>
            <span className="about_title">
              gethub: Leul-dev
            </span>
            <span className="about_title1">
              App Description:
            </span>
            <p className="about_description">This is just a simple game developed using react. 
            inspired by (code pen tic-tac-toe app) it's not efficent and effective it's just a prototype which havent use conventions and easy solutions like redux/context/hooks, i hope you like the alpha state, stay tuned for more...  :)
            </p> 
            <span className="about_date">9 Oct,2019</span>
         </div>
       </MenuCard>
     }

    return(
      <div>
				<div className="menu-card-content">
          
          {!this.state.showGame? 
            <span className="rightTopBtn" onClick={this.menuToggler.bind(this)}>&#9776;</span> : null
          }

          {this.state.showMenu ?
            <div className="menu center-items">
              <span  className="rightTopBtn fs" onClick={this.menuToggler.bind(this)}>&times;</span>
              <ul>
                <li onClick={this.openMenu.bind(this, 'play')}>
                  Play
                </li>
                <li onClick={this.openMenu.bind(this, 'help')}>
                  Help
                </li>
                <li onClick={this.openMenu.bind(this, 'about')}
                >
                  About
                </li>
              </ul>
            </div> : null
          } 
          
          {menusDisplay}
          
          <div className="menu-center center-items">
            <img
            src={!this.state.showGame ? play : pause} className="menu-center-image"
            alt="play"
            onClick={this.play.bind(this)}
            />
          </div>

				</div>
			</div>
    )
  }
}

export default GameMenu;