import React, { Component } from 'react';

class MenuCard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opened: this.props.toggleCard
    }
  }

  componentDidUpdate(prevProp) {
    if(prevProp.toggleCard!==this.props.toggleCard) {
      this.setState({opened: this.props.toggleCard});
    }
  }

  openMenu() {
    this.setState((state, props) => ({
      opened: !state.opened
    }));
    this.props.closeMenu();
  }

  render() {
    return(
      this.state.opened ?
      <div className="menu center-items menu-card-one menu-item">
        <span  className="rightTopBtn" 
        onClick={this.openMenu.bind(this)}>
          &#10551;
        </span>
        {this.props.children}
      </div> : <div></div>
    ) 
  }
}

export default MenuCard;